class ObjMeths {
    /**
     *    keyPath   obj,     
     *   defaultVal.
     * @param {object} obj      .
     * @param {string} chainOfKeys   .
     * @param defaultVal ,     keyPath  obj.
     * @returns {*}
     */
    static getValOrElse(obj, chainOfKeys, defaultVal = (key, path) => path) {
        const keyList = chainOfKeys.split(".")
        let result = obj

        for (const key of keyList) {
            result = result[key]
            if (result === undefined) {
                return defaultVal(key, chainOfKeys)
            }
        }
        return result
    }
}

export default ObjMeths
