class ArrMeths {
    /**
     *     .
     * @param {array} stringList  .
     * @param {string} separator    .
     * @param {string} prefix  .
     * @param {string} postfix  .
     * @returns {string}
     */
    static joinToString(stringList, separator = " ", prefix = "", postfix = "") {
        return `${prefix}${stringList.join(separator)}${postfix}`
    }
}

export default ArrMeths
